// https://kitsu.io/api/edge/anime?filter[text]= поиск поназванию 
// https://kitsu.io/api/edge/categories категории 

//список аниме упровление 
// first https://kitsu.io/api/edge/anime?page[limit]=5&page[offset]=0
// next  https://kitsu.io/api/edge/anime?page[limit]=5&page[offset]=5
// last https://kitsu.io/api/edge/anime?page[limit]=5&page[offset]=12062

// useHistory api 


import 'antd/dist/antd.css';
import {  useState } from 'react';
import React from 'react';
import {  DatePicker, Row, Col, Card, Space,  Input,Typography,message,Tooltip} from 'antd';


const BlocTree = () => {
    const [data , setData] = useState([])

    const { Search } = Input;
    const { Meta } = Card;
    const onSearch = (value: string) => {
        console.log(value)
        fetch(`https://kitsu.io/api/edge/anime?filter[text]=${value}`)
        .then(res => res.json())
        .then(anime => {
            setData(anime.data)
        }).catch((e) => message.error('Ошибка'))
    }
   
    return (
       <>
        <Search placeholder="input search text" onSearch={onSearch} style={{ width: 200 }} enterButton />
        <Row gutter={[1, 1]}>
        {data !== undefined ? data.map((i:any )=>{
          return <Col span={8}> <Tooltip title={i.attributes.description}>
          <Card hoverable style={{ width: 240 }} cover={<img alt="example" src={i.attributes.posterImage.small} />} >
              <Meta title={i.attributes.titles.en}
                description={`${i.attributes.startDate} // ${i.attributes.endDate !== null ?i.attributes.endDate : ''}`} />
                <br></br><h1>{i.attributes.ageRatingGuide}</h1>
                <br></br><h1>{`Episode ${i.attributes.episodeCount !== null ? i.attributes.episodeCount: 'Unknown' }`}</h1>
            </Card>
            </Tooltip>
            </Col> }):message.error('Ошибка')}
        
        </Row>
       </>
    );
}

export default BlocTree;