import 'antd/dist/antd.css';
import {  useState  } from 'react';
import {  DatePicker, Row, Col, Card, Space,  Input,Typography,message} from 'antd';

const  BlocTwo :any =  () => {
  const { Meta } = Card
  const [dataPiker, setDataPiker] = useState([])
  const [searchData, setSearchData] = useState([])
  const { Search } = Input;
  const { Title } = Typography;

  function onChange(dateString: any) { // получение значения датапикера 
    setDataPiker(dateString)
  }

  const onSearch = (value: string) => {
    fetch(`http://www.omdbapi.com/?s=${value}&y=${dataPiker}&plot=full&apikey=c5b1dac2`)
      .then(res => res.json())
      .then(movie => {
        setSearchData(movie.Search)
      }).catch((e) => message.error('Ошибка'))
  }

  return (
    <>
    <Row >
    <Col span={12} offset={6}><Title>www.omdbapi.com Api Поиск фильмов </Title></Col>
    </Row>
    <Col span={12} offset={6}>  <Space >
        <DatePicker onChange={onChange} picker="year" />
        <Search placeholder="input search text" onSearch={onSearch}  enterButton />
      </Space>
    </Col>
      <Row gutter={[1, 1]}>
        {searchData !== undefined ? searchData.map((i: any) => {
          return <Col span={8}>
            <Card key={i.imdbID} hoverable style={{ width: 240, margin: 5 }} cover={<img alt="example" src={i.Poster} />}>
              <Meta key={i.imdbID} title={i.Title} description={i.Year} />
               </Card></Col>
        }) : message.error('Ошибка')}
      </Row>
    </>
  );
}

export default BlocTwo